.header{
    width: 100%;
    background: #fff;
    text-align: center;
    padding: 30px 0;
    color: #000;
    border-bottom: 2px solid #000;
}
.header ul{
    display: flex;
    text-align: center;
    justify-content: center;
    gap: 30px;
    margin-top: 10px;
}
.header ul li{
    font-size: 20px;
    font-weight: bold;
    color: blue;
}